<template>
  <div>
    <HeaderTitle @arrowLeftTo="()=>$router.back()">当前城市</HeaderTitle>
    <van-search
      style="position: fixed;top: 44px;width:100%;z-index:1"
      v-model="SearchValue"
      placeholder="请输入搜索关键词"
      @input="onInput"
    />
    <div style="margin-top:90px" v-if="!SearchValue">
      <van-index-bar>
        <div v-for="ci in cityList" :key="ci.index">
          <van-index-anchor :index="ci.index" />
          <van-cell
            v-for="c in ci.cityArr"
            :key="c.name"
            :title="c.name"
            @click="cityToChoose(c.name,c.cityId)"
          />
        </div>
      </van-index-bar>
    </div>
    <div class="SearchCityClass" style="margin-top:90px" v-if="SearchEndCity.length>0&&SearchValue">
      <ul>
        <li
          v-for="searchCi in SearchEndCity"
          @click="cityToChoose(searchCi.name,searchCi.cityId)"
          :key="searchCi.name"
        >{{searchCi.name}}</li>
      </ul>
    </div>
    <van-empty
      style="position: absolute;top: 107px;width: 100%;text-align: center;"
      v-if="SearchEndCity.length===0&&SearchValue"
      description="搜索不到您想要的内容~"
    />
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import { instance } from "@/utils/http";
import Vue from "vue";
import { IndexBar, IndexAnchor, Cell, Search } from "vant";
import { Empty } from "vant";

Vue.use(Empty);
Vue.use(Cell);
Vue.use(IndexBar);
Vue.use(IndexAnchor);
Vue.use(Search);
export default {
  components: {
    HeaderTitle: () => import("@/components/headerTitle")
  },
  data() {
    return {
      cityList: [],
      SearchValue: "",
      SearchCityList: [],
      SearchEndCity: []
    };
  },
  methods: {
    ...mapMutations("cityStore", ["changeCity", "changeCityId"]),
    cityToChoose(city, id) {
      this.changeCity(city);
      this.changeCityId(id);
      this.$router.back();
    },
    onInput(a) {
      this.SearchEndCity = this.SearchCityList.filter(
        item =>
          item.name.includes(a) ||
          item.pinyin[0].toUpperCase().includes(a.toUpperCase())
      );
    },
    filterCity(data) {
      let letterArr = [];
      let cityObj = [];
      //生成26个英文字母
      for (var i = 65; i < 91; i++) {
        letterArr.push(String.fromCharCode(i));
      }
      for (var j = 0; j < letterArr.length; j++) {
        if (letterArr[j]) {
          cityObj.push({
            index: letterArr[j],
            cityArr: data.filter(
              item => item.pinyin[0].toUpperCase() == letterArr[j].toUpperCase()
            )
          });
        }
      }
      this.cityList = cityObj;
    }
  },
  computed: {
    ...mapState("cityStore", ["defaultCity"])
  },
  created() {
    instance("/gateway?k=7656692", {
      headers: {
        "X-Host": "mall.film-ticket.city.list"
      }
    }).then(res => {
      this.SearchCityList = res.data.data.cities;
      this.filterCity(res.data.data.cities);
    });
  }
};
</script>
<style lang="scss">
.SearchCityClass {
  box-sizing: border-box;
  ul {
    font-size: 14px;
    li {
      padding: 15px;
      border-bottom: 1px solid #ededed;
    }
  }
}
</style>